import update from 'immutability-helper';
import { handleActions } from 'redux-actions';

export default handleActions(
	{
		ADD_TODO: (state, { type, payload }) =>
			update(state, {
				$push: [
					{
						id: payload.id,
						text: payload.text,
						completed: false,
					},
				],
			}),

		TOGGLE_TODO: (state, { type, payload }) =>
			state.map(
				todo =>
					todo.id === payload.id
						? update(todo, {
								completed: { $set: !todo.completed },
							})
						: todo,
			),
	},
	[],
);
